@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap);
:root {
  --color-primary: rgba(128, 0, 0, 0.8);
  --color-secondary: #a84646;
  --secondary-hover: rgb(153, 63, 63);
  --color-accent: rgb(26, 170, 170, 0.9);
  --accent-hover: rgb(26, 170, 170);
  --primary-hover: rgba(128, 0, 0, 0.9);
  --text-muted: rgb(94, 92, 92);
  --light-gray: rgb(225, 225, 225, 0.35);
  --outline-gray: #c4c4c4;
  --nav-height: 48px;
  --background-yellow: rgb(250, 234, 211);
}

* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  background: whitesmoke;
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  color: rgb(34, 39, 63);
}

main {
  min-height: 100vh;
}

/* Post/Comment/Profile */

.post,
.comment {
  margin-top: 20px;
}

.post__body,
.comment__body {
  max-height: 500px;
  overflow-y: scroll;
}

.post__body p,
.comment__body p {
  white-space: pre;
}

.post .media {
  margin-top: 10px;
}

.content-details {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.post__header,
.comment__header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.post__details,
.comment__details {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: right;
          justify-content: right;
  margin-top: 10px;
}

.username {
  font-weight: 500;
}

.date {
  font-size: 90%;
  color: rgb(94, 92, 92);
  color: var(--text-muted);
}

.media {
  max-width: 80%;
  max-height: 80%;
}

.posts-container,
.profile-card,
.profile-edit-page .file-chooser,
.profile-edit__header,
.edit-row {
  min-width: 300px;
  max-width: 500px;
  width: 100%;
  padding: 0 1px;
}

.load-more-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 25px;
}

/* Inputs */

.text-box {
  padding: 1.5rem 2rem;
  border: 1px solid #c4c4c4;
  border: 1px solid var(--outline-gray);
  border-radius: 7px;
  outline: none;
  width: 100%;
}

.text-box:focus {
  box-shadow: 0 0 3px 1px #a84646;
  box-shadow: 0 0 3px 1px var(--color-secondary);
}

input[type='date'] {
  outline: 0;
  border: 1px solid #c4c4c4;
  border: 1px solid var(--outline-gray);
  border-radius: 5px;
  padding: 0.5rem 1rem;
}

input[type='date']:focus {
  box-shadow: 0 0 1px 1px #a84646;
  box-shadow: 0 0 1px 1px var(--color-secondary);
}

.input-group {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-right: 10px;
  box-shadow: 0 0 1px 1px #c4c4c4;
  box-shadow: 0 0 1px 1px var(--outline-gray);
  border-radius: 7px;
  background: white;
}

.input-group:focus-within {
  box-shadow: 0 0 3px 1px #a84646;
  box-shadow: 0 0 3px 1px var(--color-secondary);
}

.input-group .text-box {
  outline: none;
  border: none;
}

.input-group .text-box:focus {
  box-shadow: none;
}

.input-group .icon {
  margin-left: 5px;
}

/* Comments */

.create-comment {
  margin-top: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.create-comment .btn {
  margin-top: 5px;
}

.create-comment .text-box {
  resize: vertical;
  min-height: 50px;
  height: 50px;
  max-height: 200px;
}

/* Alerts */

.alert {
  padding: 1rem 0;
  border-radius: 5px;
  margin-bottom: 1rem;
  text-overflow: clip;
  width: 100%;
}

.alert--primary {
  color: rgba(128, 0, 0, 0.8);
  color: var(--color-primary);
}

.alert--secondary {
  color: #a84646;
  color: var(--color-secondary);
}

.alert--accent {
  color: rgb(26, 170, 170, 0.9);
  color: var(--color-accent);
}

/* General/Misc */

.clickable {
  cursor: pointer;
}

.page {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 3rem;
}

.page-form {
  background: rgb(250, 234, 211);
  background: var(--background-yellow);
  min-height: 100vh;
}

.icon {
  margin-left: 20px;
}
.icon--small {
  font-size: 1.6rem;
}

.icon--medium {
  font-size: 2.2rem;
}

.icon--large {
  font-size: 2.5rem;
}

/* Login/Register */

.login-page img,
.register-page img {
  display: none;
}

@media screen and (min-width: 960px) {
  .login-page,
  .register-page {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
  }
  .login-page img,
  .register-page img {
    display: inline-block;
    max-width: 600px;
    width: 100%;
  }
}

.btn {
  padding: 1rem 2rem;
  outline: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  white-space: nowrap;
  text-align: center;
}

.btn--corner {
  border-radius: 0;
}

.btn--block {
  display: inline-block;
  width: 100%;
}

.btn--rounded {
  border-radius: 30px;
}

.btn--outline {
  background: transparent;
  border: 1px solid rgb(14, 8, 95);
  transition: background-color 0.05s;
}

.btn--outline:hover {
  background: rgb(14, 8, 95);
  color: whitesmoke;
}

.btn--primary {
  background: var(--color-primary);
  color: whitesmoke;
}

.btn--primary:hover {
  background: var(--primary-hover);
}

.btn--secondary {
  background: var(--color-secondary);
  color: whitesmoke;
}

.btn--secondary:hover {
  background: var(--secondary-hover);
}

.btn--accent {
  background: var(--color-accent);
  color: whitesmoke;
}

.btn--accent:hover {
  background: var(--accent-hover);
}

.btn--small {
  padding: 0.75rem 1.25rem;
}

.btn--medium {
  font-size: 2rem;
}
.btn--large {
  font-size: 2.4rem;
}

.create-post__body .text-box {
  min-height: 100px;
  max-height: 250px;
  resize: vertical;
  margin-bottom: 10px;
}

.create-post__header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
}
.create-post__footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem 0;
}

.create-post__footer .icon {
  transition: color 0.1s;
  margin: 0;
}

.create-post__footer .icon:hover {
  color: var(--color-primary);
}

.post-search {
  width: 275px;
}

.search-date-range {
  display: grid;
  grid-template-columns: 1fr 9fr;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 10px 10px;
  gap: 10px 10px;
}

.search-date-range .label {
  justify-self: right;
}

.list-group {
  list-style: none;
  text-align: left;
  padding: 0;
  box-shadow: 0 0 2px 1px var(--outline-gray);
  border-radius: 10px;
  overflow: hidden;
}

.list-group__item {
  padding: 1rem;
}

.list-group__item--active {
  background: var(--color-secondary);
  color: whitesmoke;
}

.hide {
  display: none;
}

.animate__animated.animate__fadeIn {
  --animate-delay: 0;
  --animate-duration: 0.5s;
}

@media screen and (min-width: 1000px) {
  .posts-page {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
  }
  .post-search {
    margin-right: 100px;
  }
}

.card {
  border-radius: 7px;
  overflow: hidden;
  box-shadow: 0 0 5px 2px lightgray;
}

.card__header,
.card__body {
  padding: 1.5rem 2.5rem;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown__content {
  display: none;
  z-index: 100;
  position: absolute;
  right: 5px;
  border: 0.5px solid gray;
}

.dropdown__content .btn {
  transition: background-color 0.2s;
}
.dropdown__content .btn:hover {
  background: rgb(225, 225, 225);
}

.show {
  display: block;
}

.form {
  padding: 10px;
  width: 300px;
}

.form .label,
.form .text-box {
  display: block;
}

.form__group {
  margin-bottom: 2rem;
}

.form__group .icon {
  transition: color 0.1s;
}

.form__group .icon:hover {
  color: var(--color-accent);
}

.label {
  margin-bottom: 10px;
}

.form h2 {
  text-align: center;
}

.form .text-box:is(textarea) {
  resize: vertical;
  min-height: 100px;
  height: 100px;
  max-height: 250px;
}

.nav {
  background: var(--color-secondary);
  height: var(--nav-height);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: -webkit-sticky;
  position: sticky;
  height: var(--nav-height);
  z-index: 20;
  top: 0;
}

.nav__container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: var(--nav-height);
}

.nav__brand {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-decoration: none;
  color: whitesmoke;
  margin-left: 3rem;
}

.nav__logo {
  width: 25px;
  height: 25px;
  margin-right: 10px;
}

.nav__link {
  padding: 0.5rem 1rem;
  text-decoration: none;
  color: whitesmoke;
}

.nav__menu {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav__item {
  padding: 1rem 2rem;
  line-height: 30px;
  transition: all 0.3s ease;
}

.nav__item::after {
  content: '';
  display: block;
  height: 2px;
  width: 0;
  background: transparent;
  transition: all 0.3s ease-in;
}

.nav__item:hover::after {
  width: 100%;
  background: whitesmoke;
}

.nav__item .active {
  color: whitesmoke;
  border: 1.5px solid whitesmoke;
}

.nav__menu-icon {
  color: whitesmoke;
  display: none;
}

@media screen and (max-width: 768px) {
  .nav__menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    position: absolute;
    top: var(--nav-height);
    padding: 0;
    margin: 0;
    position: absolute;
    left: -110%;
    transition: all 0.5s ease;
  }

  .nav__menu.active {
    background: var(--color-secondary);
    left: 0;
  }
  .nav__menu-icon {
    display: block;
  }

  .nav__link {
    width: 100%;
    display: table;
  }
}

.profile-card__header {
  border-radius: 7px;
  box-shadow: 0 0 5px 1px var(--outline-gray);
  background: rgba(0, 128, 128, 0.01);
}

.profile-card__body {
  padding-left: 0;
  padding-right: 0;
}
.profile-details {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 3rem;
}

.profile-details .username {
  margin-right: 1rem;
}

.profile-simple {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
}

.profile-simple:hover {
  background: var(--light-gray);
}

.tabnav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

.tabnav__item {
  padding: 10px 15px;
  border-radius: 20px;
  transition: background-color 0.05s;
}

.tabnav__item--active {
  background: var(--accent-hover);
  color: whitesmoke;
}

.profile-edit__header {
  font-size: 2rem;
}

.profile-edit-page div:not(:first-child) {
  display: grid;
  grid-template-columns: 4fr 8fr 2fr;
  -webkit-align-items: center;
          align-items: center;
}

.edit-row .icon {
  justify-self: center;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.edit-row:not(.profile-edit__photo):hover .icon {
  -webkit-transform: translateX(5px) scale(1.2);
          transform: translateX(5px) scale(1.2);
}

.edit-row.profile-edit__photo .icon {
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.edit-row.profile-edit__photo:hover .icon {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.profile-edit-page div {
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 5px;
}

.profile-edit-page .file-chooser,
.profile-edit-page .file-chooser div {
  padding: 0;
}

.profile-edit-page .file-chooser div {
  padding-left: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.profile-edit-page div:not(:last-child) {
  border-bottom: 1px lightgray solid;
}

.profile-edit-page .file-chooser div {
  grid-column: 1 / span 3;
}

.profile-edit__username .edit-row__text,
.profile-edit__email .edit-row__text,
.profile-edit__description .edit-row__text {
  margin-left: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
  max-width: 100%;
  white-space: nowrap;
}

